import React, { useState } from "react";
import {
  Grid,
  makeStyles,
  Stepper,
  Step,
  StepLabel,
  StepContent,
  Button,
  Typography,
} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  topSpace: {
    marginTop: theme.spacing(10),
  },
  leftSpace: {
    marginLeft: theme.spacing(10),
  },
}));

const steps = ["Planering", "Montering", "Garanti"];

const getStepContent = (step) => {
  switch (step) {
    case 0:
      return `Under v√•rt f√∂rsta kostnadsfria bes√∂k hos er diskuterar vi olika l√∂sningar, m√§ter och sedan tar vi fram en offert. V√•rat m√•l √§r att hitta just er perfekta l√∂sning. Vi kommer √§ven g√• igenom olika finansieringsl√∂sningar.`;
    case 1:
      return "Vi best√§mmer tillsammans ett datum f√∂r montering. Vi kommer att s√§kerst√§lla att alla m√•tt √§r exakta samt att den t√§nkta l√∂sningen uppfyller era krav. En av v√•ra specialister kommer sedan hem till er f√∂r att montera den nya inglasningen.";
    case 2:
      return `Vi har 5 √•rs garanti f√∂r b√•de v√•ra produkter och sj√§lva monteringen. Produkterna vi arbetar med √§r CE-m√§rkta s√§kerhetsglas.`;
    default:
      return "Unknown step";
  }
};

const HowToStepper = () => {
  const [activeStep, setActiveStep] = useState(0);
  const classes = useStyles();

  const handleNext = () => {
    setActiveStep((prev) => prev + 1);
  };

  const handlePrev = () => {
    setActiveStep((prev) => prev - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Stepper activeStep={activeStep} orientation="vertical">
          {steps.map((step, index) => (
            <Step key={step}>
              <StepLabel>
                <Typography variant="h2">{step}</Typography>
              </StepLabel>
              <StepContent>
                <Typography variant="body1">{getStepContent(index)}</Typography>
                <Grid item xs={6} className={classes.topSpace}>
                  <Button
                    size="small"
                    disabled={activeStep === 0}
                    onClick={handlePrev}
                  >
                    Tillbaka
                  </Button>
                  <Button
                    size="small"
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.leftSpace}
                    disabled={activeStep === steps.length - 1}
                  >
                    N√§sta
                  </Button>
                </Grid>
              </StepContent>
            </Step>
          ))}
        </Stepper>
        {activeStep === steps.length && (
          <Grid item xs={12} className={classes.leftSpace}>
            <Typography variant="body1">S√• l√§tt var det!</Typography>
            <Button
              size="small"
              onClick={handleReset}
              className={classes.topSpace}
            >
              Tillbaka
            </Button>
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default HowToStepper;
